import {createCustomElement} from '@seismic/core';
import snabbdom from '@seismic/snabbdom-renderer';
import style from './style.scss';
import '@servicenow/now-alert';


const view = (state) => {

    let {
        status,
        content,
    } = state.properties;
	
	return (
        <now-alert className="mitre-matrix-notification" status={status} icon="bell-outline" content={content} action={{type: "dismiss"}} />
    );
};

createCustomElement('sn-mitre-heatmap-notification', {
    styles: style,
	renderer: {type: snabbdom},
	view,
	properties: {
        status: "",
        content: "",
    }
});