
export const generateUrlFilter = (priorities, time, NonfalsePositive) => {
    let encodedFilters = "";
    
    if (priorities && priorities.length > 0) {
        // ^incident.priorityIN1,2
        priorities = priorities.map(item => item.id).join();
        encodedFilters += `^incident.priorityIN${priorities}`;
    }

    if (time && time.start && time.end) {
        // ^sys_created_onBETWEENjavascript:gs.dateGenerate('2020-07-06','00:00:00')@javascript:gs.dateGenerate('2020-07-17','23:59:59')
        encodedFilters += `^incident.sys_created_onBETWEENjavascript:gs.dateGenerate('${time.start}','00:00:00')@javascript:gs.dateGenerate('${time.end}','23:59:59')`
    }

    if (NonfalsePositive) {
        encodedFilters += `^incident.close_code!=2^ORincident.active=true`;
    }

    return encodedFilters;
};

export const generateCellColor = (cell, displayCoverage) => {
    if (displayCoverage !== "true") return "";

    return cell.coverageColor ? cell.coverageColor : "grey";
};

export const generateBorder = (cell) => {
    // if last sub-technique
    if (cell.isLastChild)
        return "0 1px 0 0 #888 inset, 0px 3px 0px 0px black, 3px 0px 0px 0px black, 3px 0px 0px 0px black inset";

    // if sub-technique
    if (cell.isSubTechnique)
        return "0 1px 0 0 #888 inset, 0 1px 0 0 #888, 3px 0px 0px 0px black, 3px 0px 0px 0px black inset";

    // if parent technique
    if (cell.isParentTechnique)
        return "0 1px 0 0 #888, 0px 3px 0px 0px black inset, 3px 0px 0px 0px black, 3px 0px 0px 0px black inset";

    return "1px 0 0 0 #888, 0 1px 0 0 #888, 1px 1px 0 0 #888, 1px 0 0 0 #888 inset, 0 1px 0 0 #888 inset";
};

export const covert2hex = (str, alpha) => {
    let ctx = document.createElement("canvas").getContext("2d");
    ctx.fillStyle = str;
    if (alpha)
        return ctx.fillStyle + alpha;
    
    return ctx.fillStyle;
};

const _RGB_Log_Shade=(p,c)=>{
    var i=parseInt,r=Math.round,[a,b,c,d]=c.split(","),P=p<0,t=P?0:p*255**2,P=P?1+p:1-p;
    return"rgb"+(d?"a(":"(")+r((P*i(a[3]=="a"?a.slice(5):a.slice(4))**2+t)**0.5)+","+r((P*i(b)**2+t)**0.5)+","+r((P*i(c)**2+t)**0.5)+(d?","+d:")");
}

export const RGB_Log_Shade=(hex, p)=>{
    let R = parseInt(hex.substring(1,3),16),
        G = parseInt(hex.substring(3,5),16),
        B = parseInt(hex.substring(5,7),16),
        rgb = "rgb(" + R + "," + G + "," + B + ")";

    return _RGB_Log_Shade(p, rgb);
}

export const arrayContainsSubStr = (array, str) => {
    let answer = false;
    for (let i = 0; i < array.length; i++) {
        answer = array[i].indexOf(str) !== -1;
        if (answer) break;
    }

    return answer;
};

export const getFilteredOptions = (setting, matrixId, filters, type) => {
    let selectedOptions = setting && setting[matrixId] && setting[matrixId][type];
    selectedOptions = selectedOptions ? selectedOptions : [];
    let selectedOptionIds = setting && setting[matrixId] && selectedOptions && selectedOptions.map(item => item.id);
    selectedOptionIds = selectedOptionIds ? selectedOptionIds : [];
	let originalOptions = filters[type];
	return {
        selectedOptions: selectedOptions,
        unselectedOptions: originalOptions && originalOptions.filter(item => selectedOptionIds.length === 0 || (selectedOptionIds && selectedOptionIds.indexOf(item.id) === -1))
    };
};

export const syncScrollToHeader = (e) => {
    const currentLeft = e.target.scrollLeft;
    const headerElement = e.target.children[0].children[0].shadowRoot.querySelector(".mitre-matrix-table-header-container");
    headerElement.scrollTo({left: currentLeft});
};