import {createCustomElement} from '@seismic/core';
import snabbdom from '@seismic/snabbdom-renderer';
import { t } from 'sn-translate';
import style from './style.scss';
import {
	POLL_MATRIX_DATA,
	SHOW_ID_FILTER_TYPE,
	SHOW_COVERAGE_FILTER_TYPE,
	SHOW_SI_FILTER_TYPE,
	SHOW_VUL_FILTER_TYPE,
    HIDE_FALSE_POSITIVE_SI_TYPE,
    SHOW_DETECTION_RULE_TYPE,
    SHOW_ANALYZE_SI,
	SI_NUMBER_FILTER_INPUT_CHANGE,
    RESTORE_VIEW,
    TOGGLE_FILTER_PANEL,
    TOGGLE_FILTER_PANEL_SECTION,
    START_LOADING,
} from '../constants';
import {getFilteredOptions} from '../helper.js';

import 'sn-devx-components';
import '@servicenow/now-icon';
import '@servicenow/now-button';
import '@servicenow/now-toggle';
import '@servicenow/now-dropdown';
import '@servicenow/now-collapse';
import '@servicenow/now-date-range-picker';
import '@devsnc/sn-record-control-wrapper';

const styleOverwrite = {
    margin0: {margin: "0px"},
    marginTop24: {marginTop: "24px"},
    marginTop12: {marginTop: "12px"},
    filterLabel: {fontSize: "12px", color: "#637274"},
};

const view = (state, {dispatch}) => {

    let {
        showFilterPanel,
        hasCveTable,
		matrixId,
		filters,
		setting,
		expand,
    } = state.properties;

    let displayCoverage = setting[matrixId] && setting[matrixId].displayCoverage;
    let displayIncidentCount = setting[matrixId] && setting[matrixId].displayIncidentCount;
    let displayDetectionRule = setting[matrixId] && setting[matrixId].displayDetectionRule;
    let displayAnalyzeSI = setting[matrixId] && setting[matrixId].displayAnalyzeSI;

	let intrusionSetOptions = getFilteredOptions(setting, matrixId, filters, "intrusion-set");
	let toolOptions = getFilteredOptions(setting, matrixId, filters, "tool");
	let malwareOptions = getFilteredOptions(setting, matrixId, filters, "malware");
	let priorityOptions = getFilteredOptions(setting, matrixId, filters, "priority");
	let platformOptions = getFilteredOptions(setting, matrixId, filters, "x_mitre_platforms");
    let dataSourceOptions = getFilteredOptions(setting, matrixId, filters, "x_mitre_data_source");
	
	return (
        <div className="mitre-matrix-filter-container" style={{display: showFilterPanel ? "" : "none"}}>
            <div className="mitre-matrix-filter-header">
                <h2 style={styleOverwrite.margin0}>{t("Filters")}</h2>
                <now-dropdown
                    className="mitre-matrix-filter-menu-btn"
                    variant="secondary"
                    select="single"
                    icon="ellipsis-h-fill"
                    size="md"
                    items={[
                        {id: "save-view", label: t("Make this as default view")},
                        {id: "clear-all", label: t("Clear all filters")},
                    ]}
                    disabled={matrixId ? false : true}
                    hideCaret
                    hideLabel
                    bare
                />
                <now-button
                    className="mitre-matrix-filter-close-btn"
                    variant="tertiary"
                    icon="close-fill"
                    size="md"
                    on-click={() => {dispatch(TOGGLE_FILTER_PANEL)}}
                    bare
                />
            </div>
            <div className="mitre-matrix-filter-panel-container">
                <div className="mitre-matrix-filter-panel-header">
                    <div className="mitre-matrix-filter-panel-header-text">{t("Primary")}</div>
                    <now-icon
                        className="mitre-matrix-filter-panel-header-toggle"
                        icon={expand.basic ? "chevron-up-fill" : "chevron-down-fill"}
                        on-click={() => {
                            dispatch(TOGGLE_FILTER_PANEL_SECTION, {section: "basic"});
                        }}
                    />
                </div>
                <now-collapse id="mitre-matrix-basic-filter" expanded={expand.basic}>
                    <div className="basic-filter-group-container">
                        {
                            filters["intrusion-set"] && filters["intrusion-set"].length > 0 ?
                            <sn-record-control-wrapper label={t("Adversary Group")} className="filter-item-control">
                                <devx-multiselect
                                    className="filter-item"
                                    items={intrusionSetOptions.unselectedOptions}
                                    selectedItems={intrusionSetOptions.selectedOptions}
                                    size="md"
                                    itemDisplayKeyName="label"
                                    itemIdKeyName="id"
                                />
                            </sn-record-control-wrapper>
                            : ""
                        }
                        {
                            filters["tool"] && filters["tool"].length > 0 ?
                            <sn-record-control-wrapper label={t("Tool")} className="filter-item-control">
                                <devx-multiselect
                                    className="filter-item"
                                    items={toolOptions.unselectedOptions}
                                    selectedItems={toolOptions.selectedOptions}
                                    size="md"
                                    itemDisplayKeyName="label"
                                    itemIdKeyName="id"
                                />
                            </sn-record-control-wrapper>
                            : ""
                        }
                        {
                            filters["malware"] && filters["malware"].length > 0 ?
                            <sn-record-control-wrapper label={t("Malware")} className="filter-item-control">
                                <devx-multiselect
                                    className="filter-item"
                                    items={malwareOptions.unselectedOptions}
                                    selectedItems={malwareOptions.selectedOptions}
                                    size="md"
                                    itemDisplayKeyName="label"
                                    itemIdKeyName="id"
                                />
                            </sn-record-control-wrapper>
                            : ""
                        }
                        {
                            filters["x_mitre_platforms"] && filters["x_mitre_platforms"].length > 0 ?
                            <sn-record-control-wrapper label={t("Platform")} className="filter-item-control">
                                <devx-multiselect
                                    className="filter-item"
                                    items={platformOptions.unselectedOptions}
                                    selectedItems={platformOptions.selectedOptions}
                                    size="md"
                                    itemDisplayKeyName="label"
                                    itemIdKeyName="id"
                                />
                            </sn-record-control-wrapper>
                            : ""
                        }
                        {
                            filters["x_mitre_data_source"] && filters["x_mitre_data_source"].length > 0 ?
                            <sn-record-control-wrapper label={t("Data Source")} className="filter-item-control">
                                <devx-multiselect
                                    className="filter-item"
                                    items={dataSourceOptions.unselectedOptions}
                                    selectedItems={dataSourceOptions.selectedOptions}
                                    size="md"
                                    itemDisplayKeyName="label"
                                    itemIdKeyName="id"
                                />
                            </sn-record-control-wrapper>
                            : ""
                        }
                    </div>
                </now-collapse>
                <div className="mitre-matrix-filter-panel-header">
                    <div className="mitre-matrix-filter-panel-header-text">{t("Advanced")}</div>
                    <now-icon
                        className="mitre-matrix-filter-panel-header-toggle"
                        icon={expand.advanced ? "chevron-up-fill" : "chevron-down-fill"}
                        on-click={() => {
                            dispatch(TOGGLE_FILTER_PANEL_SECTION, {section: "advanced"});
                        }}
                    />
                </div>
                <now-collapse id="mitre-matrix-advanced-filter" expanded={expand.advanced}>
                    <div className="advanced-filter-group-container">
                        <div className="toggle-filter-container">
                            <span className="toggle-filter-label">{t('Display technique IDs')}</span>
                            <now-toggle append-to-payload={{type: SHOW_ID_FILTER_TYPE}} checked={setting[matrixId] && setting[matrixId].displayID} size="md" className="toggle-filter" />
                        </div>
                        <div className="toggle-filter-container">
                            <span className="toggle-filter-label">{t('Display technique detection coverage')}</span>
                            <now-toggle append-to-payload={{type: SHOW_COVERAGE_FILTER_TYPE}} checked={displayCoverage} size="md" className="toggle-filter" />
                        </div>
                        <div className="toggle-filter-container">
                            <span className="toggle-filter-label">{t('Display security incident associated with technique')}</span>
                            <now-toggle append-to-payload={{type: SHOW_SI_FILTER_TYPE}} checked={displayIncidentCount} size="md" className="toggle-filter" />
                        </div>
                        {
                            displayIncidentCount ?
                            <sn-record-control-wrapper label={t("Security Incident Priority")} className="filter-item-control">
                                <devx-multiselect
                                    className="filter-item"
                                    items={priorityOptions.unselectedOptions}
                                    selectedItems={priorityOptions.selectedOptions}
                                    size="md"
                                    itemDisplayKeyName="label"
                                    itemIdKeyName="id"
                                />
                            </sn-record-control-wrapper> : ""
                        }
                        {
                            displayIncidentCount ?
                            <sn-record-control-wrapper label={t("Security Incident Date Range")} className="filter-item-control">
                                <now-date-range-picker
                                    className="filter-item"
                                    labelStart="From"
                                    labelEnd="To"
                                    start={setting[matrixId] && setting[matrixId]["time"] ? setting[matrixId]["time"].start : ""}
                                    end={setting[matrixId] && setting[matrixId]["time"] ? setting[matrixId]["time"].end : ""}
                                />
                            </sn-record-control-wrapper> : ""
                        }
                        {
                            displayIncidentCount ?
                            <div className="toggle-filter-container" style={styleOverwrite.marginTop24}>
                                <span className="toggle-filter-label">{t('Filter false positive security incident')}</span>
                                <now-toggle append-to-payload={{type: HIDE_FALSE_POSITIVE_SI_TYPE}} checked={setting[matrixId] && setting[matrixId].hideFalsePositive} size="md" className="toggle-filter" />
                            </div> : ""
                        }
                        <div className="toggle-filter-container">
                            <span className="toggle-filter-label">{t('Display detection rules')}</span>
                            <now-toggle append-to-payload={{type: SHOW_DETECTION_RULE_TYPE}} checked={displayDetectionRule} size="md" className="toggle-filter" />
                        </div>
                        {
                            hasCveTable ?
                            <div className="toggle-filter-container">
                                <span className="toggle-filter-label">{t('Display CVEs associated with technique')}</span>
                                <now-toggle append-to-payload={{type: SHOW_VUL_FILTER_TYPE}} checked={setting[matrixId] && setting[matrixId].displayVulCount} size="md" className="toggle-filter" />
                            </div> : ""
                        }
                        <div className="toggle-filter-container">
                            <span className="toggle-filter-label">{t('Analyze security incidents')}</span>
                            <now-toggle append-to-payload={{type: SHOW_ANALYZE_SI}} checked={displayAnalyzeSI} size="md" className="toggle-filter" />
                        </div>
                        {
                            displayAnalyzeSI ?
                            <sn-record-control-wrapper label={`${t("Enter Security Incident Number")}(s)`} className="filter-item-control">
                                <input
                                    on-input={(event) => {dispatch(SI_NUMBER_FILTER_INPUT_CHANGE, {newString: event.target.value})}}
                                    className="mitre-matrix-analyze-input"
                                    value={setting[matrixId] && setting[matrixId].incidentNumber ? setting[matrixId].incidentNumber : ""}
                                />
                            </sn-record-control-wrapper> : ""
                        }
                    </div>
                </now-collapse>
            </div>
            <div className="filter-button-container">
                <now-button
                    className="filter-button"
                    label={t("Restore Default Filters")}
                    variant="secondary"
                    on-click={() => {dispatch(RESTORE_VIEW)}}
                    style={{width: "170px"}}
                />
                <now-button
                    className="filter-button"
                    label={t("Apply")}
                    variant="primary"
                    disabled={matrixId === null}
                    on-click={() => {
                        dispatch(START_LOADING);
                        dispatch(POLL_MATRIX_DATA, {matrixId: matrixId, filters: setting[matrixId]});
                    }}
                    style={{width: "170px"}}
                />
            </div>
        </div>
    );
};

createCustomElement('sn-mitre-heatmap-filter-panel', {
    styles: style,
	renderer: {type: snabbdom},
	view,
	properties: {
        showFilterPanel: false,
        hasCveTable: false,
		matrixId: "",
		filters: {},
		setting: {},
		expand: {},
    }
});