import {createCustomElement} from '@seismic/core';
import snabbdom from '@seismic/snabbdom-renderer';
import '@servicenow/now-tooltip';
import { t } from 'sn-translate';
import './sn-mitre-heatmap-subtechnique-cell';
import style from './style.scss';
import {
    generateUrlFilter,
    covert2hex,
    arrayContainsSubStr
} from '../helper.js';

import {
    noneCoverage,
    poorCoverage,
    fairCoverage,
    goodCoverage,
    veryGoodCoverage,
    excellentCoverage,
} from '../assets';

const view = (state, {dispatch}) => {
    const {properties} = state;
    const {
        setting,
        search,
        technique,
        tacticId,
    } = properties;
    
    const setTargetRef = (ref) => dispatch('PROPERTIES_SET', {targetRef: ref});
    const isSearching = search && search.length > 0;
    const displayCoverage = setting && setting.displayCoverage;

    let overwriteStyle;
    if (displayCoverage)
        overwriteStyle = {color: "black"};

    let overwriteIDStyle;
    if (displayCoverage)
        overwriteIDStyle = {color: "black"};

	return (
        <div
            className="mitre-matrix-technique-group"
            style={{
                backgroundColor: displayCoverage && technique.coverageColor && technique.coverageColor.length > 0 ? `${covert2hex(technique.coverageColor)}` : "",
                display: isSearching &&
                    technique.name.toLowerCase().indexOf(search.toLowerCase()) === -1 &&
                    technique.external_id.toLowerCase().indexOf(search.toLowerCase()) === -1 &&
                    !arrayContainsSubStr(technique.subTechniques.map(item => item.name.toLowerCase()), search.toLowerCase()) &&
                    !arrayContainsSubStr(technique.subTechniques.map(item => item.external_id.toLowerCase()), search.toLowerCase()) ? "none" : "",
            }}
        >
            {
                <div
                    className="mitre-matrix-technique"
                    style={{
                        display: isSearching &&
                            technique.name.toLowerCase().indexOf(search.toLowerCase()) === -1 &&
                            technique.external_id.toLowerCase().indexOf(search.toLowerCase()) === -1 ? "none" : ""
                    }}
                >
                    <div
                        className="technique-name"
                        hook-insert={({elm}) => setTargetRef(elm)}
                        hook-destroy={() => setTargetRef(null)}
                        aria-describedby={`${technique.id}-tooltip`}
                    >
                        <a
                            className="technique-name-link"
                            href={`/sn_ti_stix2_attack_pattern.do?sys_id=${technique.id}`}
                            target="_blank"
                            style={overwriteStyle ? overwriteStyle : {width: "100%"}}
                        >
                            <b>{technique.name}</b>
                        </a>
                        {
                            displayCoverage && technique.coverageName === "Excellent" ?
                            <img className="coverage-icon" src={excellentCoverage} /> : ""
                        }
                        {
                            displayCoverage && technique.coverageName === "Very Good" ?
                            <img className="coverage-icon" src={veryGoodCoverage} /> : ""
                        }
                        {
                            displayCoverage && technique.coverageName === "Good" ?
                            <img className="coverage-icon" src={goodCoverage} /> : ""
                        }
                        {
                            displayCoverage && technique.coverageName === "Fair" ?
                            <img className="coverage-icon" src={fairCoverage} /> : ""
                        }
                        {
                            displayCoverage && technique.coverageName === "Poor" ?
                            <img className="coverage-icon" src={poorCoverage} /> : ""
                        }
                        {
                            displayCoverage && technique.coverageName === "None" ?
                            <img className="coverage-icon" src={noneCoverage} /> : ""
                        }
                    </div>
                    <span className="technique-meta-data" style={setting && setting.displayID ? (overwriteIDStyle ? overwriteIDStyle : {}) : {display: "none"}}>
                        {technique.external_id}
                    </span>
                    <span className="technique-meta-data" style={setting && setting.displayIncidentCount && !setting.hideFalsePositive ? {} : {display: "none"}}>
                        {
                            technique.incidentCount > 0 ?
                            <a style={overwriteStyle ? {...overwriteStyle, textDecoration: "underline"} : {}} className="count-link" href={`/sn_ti_stix2_m2m_incident_attack_list.do?sysparm_query=attack=${technique.id}${generateUrlFilter(setting && setting.priority, setting && setting.time)}`} target="_blank">{`${t('Security Incidents: ')}${technique.incidentCount}`}</a>
                            : <span style={overwriteStyle ? {...overwriteStyle, textDecoration: "none"} : {}} className="count-link">{`${t('Security Incidents: ')}0`}</span>
                        }
                    </span>
                    <span className="technique-meta-data" style={setting && setting.displayIncidentCount && setting.hideFalsePositive ? {} : {display: "none"}}>
                        {
                            technique.incidentCountNonFp > 0 ?
                            <a style={overwriteStyle ? {...overwriteStyle, textDecoration: "underline"} : {}} className="count-link" href={`/sn_ti_stix2_m2m_incident_attack_list.do?sysparm_query=attack=${technique.id}${generateUrlFilter(setting && setting.priority, setting && setting.time, true)}`} target="_blank">{`${t('Security Incidents: ')}${technique.incidentCountNonFp}`}</a>
                            : <span style={overwriteStyle ? {...overwriteStyle, textDecoration: "none"} : {}} className="count-link">{`${t('Security Incidents: ')}0`}</span>
                        }
                    </span>
                    <span className="technique-meta-data" style={setting && setting.displayDetectionRule ? {} : {display: "none"}}>
                        {
                            technique.detRuleCount > 0 ?
                            <a style={overwriteStyle ? {...overwriteStyle, textDecoration: "underline"} : {}} className="count-link" href={`/sn_ti_alert_rules_mitre_attack_technique_mapping_list.do?sysparm_query=technique=${technique.id}^tactic=${tacticId}`} target="_blank">{`${t('Detection Rule: ')}${technique.detRuleCount}`}</a>
                            : <span style={overwriteStyle ? {...overwriteStyle, textDecoration: "none"} : {}} className="count-link">{`${t('Detection Rule: ')}0`}</span>
                        }
                    </span>
                    <span className="technique-meta-data" style={setting && setting.displayVulCount ? {} : {display: "none"}}>
                        {
                            technique.vulCount > 0 ?
                            <a style={overwriteStyle ? {...overwriteStyle, textDecoration: "underline"} : {}} className="count-link" href={`/sn_ti_stix2_m2m_attack_nvd_list.do?sysparm_query=attack=${technique.id}`} target="_blank">{`${t('CVEs: ')}${technique.vulCount}`}</a>
                            : <span style={overwriteStyle ? {...overwriteStyle, textDecoration: "none"} : {}} className="count-link">{`${t('CVEs: ')}0`}</span>
                        }
                    </span>
                    <now-tooltip
                        id={`${technique.id}-tooltip`}
                        target-ref={properties.targetRef}
                        position={['center-end center-start', 'center-start center-end']}
                        content={technique.isSubTechnique ? `${t("Sub-technique ID")} ${technique.external_id}` : `${t("Technique ID")} ${technique.external_id}`}
                    />
                </div>
            }
            <div className="mitre-matrix-subtechnique-group">
                {
                    technique.subTechniques.map(subTechnique => 
                        isSearching &&
                            subTechnique.name.toLowerCase().indexOf(search.toLowerCase()) === -1 &&
                            subTechnique.external_id.toLowerCase().indexOf(search.toLowerCase()) === -1 ? "" :
                        <sn-mitre-heatmap-subtechnique-cell
                            setting={setting}
                            subTechnique={subTechnique}
                            tacticId={tacticId}
                        />
                    )
                }
            </div>
        </div>
    );
};

createCustomElement('sn-mitre-heatmap-technique-cell', {
    renderer: {type: snabbdom},
    styles: style,
	view,
	properties: {
        setting: {},
        search: "",
        technique: {},
        tacticId: "",
        targetRef: {default: null}
    }
});