import {actionTypes} from '@seismic/core';
import {createHttpEffect} from '@seismic/effect-http';
import { t } from 'sn-translate';
import * as CONSTANTS from './constants.js';

export default {
	actionHandlers: {
		[actionTypes.COMPONENT_BOOTSTRAPPED]: ({state, updateState, dispatch}) => {
			updateState({loadingCollectionData: true});
			dispatch(CONSTANTS.INITIAL_LOAD, {data: {userSysId: state.behaviors.userData.sys_id}});
		},

		[CONSTANTS.INITIAL_LOAD]: createHttpEffect(
			'/api/sn_ti/mitre_seismic_rest_api/initial_load',
			{
				method: 'POST',
				dataParam: 'data',
				successActionType: CONSTANTS.RENDER_MATRICES_LIST,
				errorActionType: CONSTANTS.FAIL_TO_LOAD,
			}
		),

		[CONSTANTS.RENDER_MATRICES_LIST]: ({updateState, action}) => {
			const {result} = action.payload;
			let profileData = result.profile;
			profileData = profileData ? profileData : {};
			profileData = typeof profileData === "string" ? JSON.parse(profileData) : profileData;

			updateState({
				loadingCollectionData: false,
				matricesList: result.sources,
				setting: profileData,
				defaultSetting: profileData,
				hasCveTable: result.hasCVETable, // The name of the properties in Seismic can not be consecutive uppercase
			});
		},

		[CONSTANTS.FAIL_TO_LOAD]: ({updateState}) => {
			updateState({
				loading: false,
				loadingCollectionData: false,
				showFilterPanel: false,
				matrixData: {data: []},
				matrixId: null,
				matrix: [],
				setting: {},
			});

			dispatch(CONSTANTS.TRIGGER_NOTIFICATION, {
				content: t("Error when initial loading."),
				status: "warning",
			});
		},

		[CONSTANTS.POLL_MATRIX_DATA]: ({updateState, dispatch, action}) => {
			const matrixId = action.payload.matrixId,
				filters = action.payload.filters;
			
			// close panel, show loading spinner
			updateState({
				loading: true,
				showFilterPanel: false,
			});
			
			dispatch(CONSTANTS.START_POLLING_MATRIX_DATA, {matrixId: matrixId, filters: filters});
		},

		[CONSTANTS.START_POLLING_MATRIX_DATA]: createHttpEffect(
			'/api/sn_ti/mitre_seismic_rest_api/matrix/:matrixId',
			{
				method: 'POST',
				pathParams: ['matrixId'],
				dataParam: 'filters',
				successActionType: CONSTANTS.RENDER_MATRIX,
				errorActionType: CONSTANTS.RESET_MATRIX,
			}
		),

		[CONSTANTS.RESET_MATRIX]: ({updateState}) => {
			updateState({
				loading: false,
				showFilterPanel: false,
				matrixData: {data: []},
				matrixId: null,
				matrix: [],
				setting: {},
			});

			dispatch(CONSTANTS.TRIGGER_NOTIFICATION, {
				content: t("Error when rendering navigator and heatmap view."),
				status: "warning",
			});
		},

		[CONSTANTS.RENDER_MATRIX]: ({updateState, action}) => {
			// close filter panel as the first step
			updateState({loading: false});

			const {result} = action.payload;

			updateState({
				matrixData: result,
				showFilterPanel: false,
			});
		},

		[CONSTANTS.DROPDOWN_SELECT]: ({dispatch, state, updateState, action}) => {
			const matrixId = action.payload.value[0];

			// if the value is to save the view instead of matrix sys_id
			if (matrixId === "save-view") {
				dispatch(CONSTANTS.SAVE_VIEW, {
					data: {
						userSysId: state.behaviors.userData.sys_id,
						profileData: state.setting,
					},
				});
			} else if (matrixId === "clear-all") {
				dispatch(CONSTANTS.CLEAR_VIEW);
			} else {
				// initial load of the matrix
				// need to honor the saved view
				// set filter lists
				state.matricesList.forEach((matrix) => {
					let setting = state.setting;

					if (matrix.id === matrixId) {
						updateState({
							matrixId: matrixId,
							matrixLastRunDate: matrix.lastRunDate,
							filters: {
								...state.filters,
								...matrix.filters,
							},
							setting: setting,
						});

						// upon changing the source, fetch the new matrix data
						dispatch(CONSTANTS.POLL_MATRIX_DATA, {matrixId: matrixId, filters: setting[matrixId]});
					}
				});
			}
		},

		// only handle addition (workaround due to a bug in append-to-payload in devx)
		[CONSTANTS.MULTI_SELECT]: ({state, updateState, action}) => {
			let items = action.payload.value,
				type = items && items.length > 0 && items[0].type,
				matrixId = state.matrixId,
				currentItems = state.setting[matrixId] && state.setting[matrixId][type] ? state.setting[matrixId][type] : [],
				isAdd = items && matrixId && type && items.length > currentItems.length;

			if (items && type && items.length && isAdd) {
				updateState({
					path: `setting.${matrixId}.${type}`,
					value: items,
					operation: 'set'
				});
			}
		},

		// only handle removal (workaround due to a bug in append-to-payload in devx)
		[CONSTANTS.FILTER_PILL_DISMISSED]: ({state, updateState, action}) => {
			let removedItem = action.payload.item,
				type = removedItem && removedItem.type,
				matrixId = state.matrixId,
				currentItems = state.setting[matrixId][type];
			
			if (removedItem && type) {
				currentItems = currentItems.filter((item) => item.id !== removedItem.id);
				updateState({
					path: `setting.${matrixId}.${type}`,
					value: currentItems,
					operation: 'set'
				});
			}
		},

		// only handle removal (workaround due to a bug in append-to-payload in devx)
		[CONSTANTS.FILTER_PILL_BACKSPACE]: ({state, updateState, action}) => {
			let removedItem = action.payload.item,
				type = removedItem && removedItem.type,
				matrixId = state.matrixId,
				currentItems = state.setting[matrixId][type];
			
			if (removedItem && type) {
				currentItems = currentItems.filter((item) => item.id !== removedItem.id);
				updateState({
					path: `setting.${matrixId}.${type}`,
					value: currentItems,
					operation: 'set'
				});
			}
		},

		[CONSTANTS.PICK_DATE_RANGE]: ({state, updateState, action}) => {
			const {start, end} = action.payload,
				matrixId = state.matrixId;
			
			updateState({
				path: `setting.${matrixId}.time`,
				value: {start: start, end: end},
				operation: 'set'
			});
		},

		[CONSTANTS.CHECKBOX_CHECK]: ({state, updateState, action}) => {
			const {name, value} = action.payload,
				matrixId = state.matrixId;
			
			updateState({
				path: `setting.${matrixId}.${name}`,
				value: value,
				operation: 'set'
			});
		},

		[CONSTANTS.FILTER_TOGGLED]: ({dispatch, state, updateState, action}) => {
			const matrixId = state.matrixId,
				{value, type} = action.payload;
			
			if (type === CONSTANTS.SHOW_ID_FILTER_TYPE) {
				updateState({
					path: `setting.${matrixId}.displayID`,
					value: value,
					operation: 'set'
				});
			} else if (type === CONSTANTS.SHOW_COVERAGE_FILTER_TYPE) {
				updateState({
					path: `setting.${matrixId}.displayCoverage`,
					value: value,
					operation: 'set'
				});
			} else if (type === CONSTANTS.SHOW_SI_FILTER_TYPE) {
				updateState({
					path: `setting.${matrixId}.displayIncidentCount`,
					value: value,
					operation: 'set'
				});
			} else if (type === CONSTANTS.SHOW_VUL_FILTER_TYPE) {
				updateState({
					path: `setting.${matrixId}.displayVulCount`,
					value: value,
					operation: 'set'
				});
			} else if (type === CONSTANTS.HIDE_FALSE_POSITIVE_SI_TYPE) {
				updateState({
					path: `setting.${matrixId}.hideFalsePositive`,
					value: value,
					operation: 'set'
				});
			} else if (type === CONSTANTS.SHOW_DETECTION_RULE_TYPE) {
				updateState({
					path: `setting.${matrixId}.displayDetectionRule`,
					value: value,
					operation: 'set'
				});
			} else if (type === CONSTANTS.SHOW_ANALYZE_SI) {
				updateState({
					path: `setting.${matrixId}.displayAnalyzeSI`,
					value: value,
					operation: 'set'
				});

				// clear input
				dispatch(CONSTANTS.SI_NUMBER_FILTER_INPUT_CHANGE, {newString: ""})
			}
		},

		[CONSTANTS.SEARCH_AND_FILTER]: ({updateState, action}) => {
			let {newString} = action.payload;
			updateState({search: newString});
		},

		[CONSTANTS.CLEAR_SEARCH]: ({updateState}) => {
			updateState({search: ""});
		},

		[CONSTANTS.SAVE_VIEW]: createHttpEffect(
			'/api/sn_ti/mitre_seismic_rest_api/save_view',
			{
				method: 'POST',
				dataParam: 'data',
				successActionType: CONSTANTS.SAVE_VIEW_SUCCESS,
				errorActionType: CONSTANTS.SAVE_VIEW_FAIL,
			}
		),

		[CONSTANTS.SAVE_VIEW_SUCCESS]: ({dispatch, updateState, action}) => {
			const {result} = action.payload;
			updateState({
				setting: result,
				defaultSetting: result,
				showFilterPanel: false,
			});

			dispatch(CONSTANTS.TRIGGER_NOTIFICATION, {
				content: t("The default view has been set with filters applied."),
				status: "positive",
			});
		},

		[CONSTANTS.SAVE_VIEW_FAIL]: () => {
			dispatch(CONSTANTS.TRIGGER_NOTIFICATION, {
				content: t("Error on saving the view."),
				status: "positive",
			});
		},

		[CONSTANTS.TRIGGER_NOTIFICATION]: ({updateState, action}) => {
			const content = action.payload.content,
				status = action.payload.status;

			updateState({
				notification: {
					content: content,
					status: status,
					hideAlert: false,
				},
			});
		},

		[CONSTANTS.CLOSE_NOTIFICATION]: ({updateState, action}) => {
			const {type} = action.payload.action;
			if (type === "dismiss")
				updateState({notification: {hideAlert: true}});
		},

		[CONSTANTS.RESTORE_VIEW]: ({dispatch, state, updateState}) => {
			const matrixId = state.matrixId;

			// restore setting from default view
			updateState({setting: state.defaultSetting});

			// submit default settings
			dispatch(CONSTANTS.POLL_MATRIX_DATA, {matrixId: matrixId, filters: state.defaultSetting[matrixId]});
		},

		[CONSTANTS.CLEAR_VIEW]: ({dispatch, state, updateState}) => {
			const matrixId = state.matrixId;
			updateState({
				path: `setting`,
				value: {},
				operation: 'set'
			});

			// submit default settings
			dispatch(CONSTANTS.POLL_MATRIX_DATA, {matrixId: matrixId, filters: {}});
		},

		[CONSTANTS.SI_NUMBER_FILTER_INPUT_CHANGE]: ({state, updateState, action}) => {
			const matrixId = state.matrixId,
				{newString} = action.payload;
			updateState({
				path: `setting.${matrixId}.incidentNumber`,
				value: newString,
				operation: 'set'
			});
		},

		[CONSTANTS.TOGGLE_FILTER_PANEL]: ({state, updateState}) => {
			let {matrixId, showFilterPanel} = state;
			if (matrixId)
				updateState({showFilterPanel: !showFilterPanel})
		},

		[CONSTANTS.TOGGLE_FILTER_PANEL_SECTION]: ({state, updateState, action}) => {
			let {section} = action.payload;
			let {expand} = state;

			updateState({
				path: `expand.${section}`,
				value: !expand[section],
				operation: 'set'
			});
		},

		[CONSTANTS.START_LOADING]: ({updateState}) => {
			updateState({loading: true});
		},

		[CONSTANTS.STOP_LOADING]: ({updateState}) => {
			updateState({loading: false});
		},
	}
};
