import {createCustomElement} from '@seismic/core';
import snabbdom from '@seismic/snabbdom-renderer';
import { t } from 'sn-translate';
import {preUserData} from './behaviors';
import actionHandler from './actionHandler';
import style from './style.scss';
import { nowLogo, emptyPage } from './assets';
import { syncScrollToHeader } from './helper';

import '@servicenow/now-alert';
import '@servicenow/now-loader';
import '@devsnc/sn-record-annotation';

import './sn-mitre-heatmap-nav';
import './sn-mitre-heatmap-filter-panel';
import './sn-mitre-heatmap-table';
import './sn-mitre-heatmap-notification';


const CELL_WIDTH = 150;

const view = (state) => {
	let {
		loading,
		loadingCollectionData,
		showFilterPanel,
		matricesList,
		matrixId,
		matrixData,
		filters,
		search,
		setting,
		expand,
		hasCveTable,
		notification,
	} = state;

	return (
		<div className="mitre-matrix">
			<div className="now-nav">
				<div className="left-logo">
					<img src={nowLogo} height="18" />
				</div>
			</div>
			{
				notification.hideAlert ? "" :
				<sn-mitre-heatmap-notification status={notification.status} content={notification.content} />
			}
			<sn-mitre-heatmap-nav
				loadingCollectionData={loadingCollectionData}
				search={search}
				setting={setting}
				matricesList={matricesList}
				matrixId={matrixId}
				showFilterPanel={showFilterPanel}
			/>
			<sn-mitre-heatmap-filter-panel
				showFilterPanel={showFilterPanel}
				matrixId={matrixId}
				filters={filters}
				hasCveTable={hasCveTable}
				setting={setting}
				expand={expand}
			/>
			<div className="mitre-matrix-body-container" on-scroll={e => syncScrollToHeader(e)}>
				{
					!matrixId ?
					<div className="mitre-matrix-blank-container">
						<img src={emptyPage} height="300" width="300" />
						<div className="mitre-matrix-blank-text">
							<div className="mitre-matrix-blank-text-big">{t('No content to show')}</div>
							<div className="mitre-matrix-blank-text-mid">{t('Select a MITRE ATT&CK source to display heatmap')}</div>
						</div>
					</div> : ""
				}
				{
					loading ?
					<div className="mitre-matrix-blank-container">
						<now-loader label="Loading"></now-loader>
					</div>
					:
					<div id="mitre-matrix-table-container">
						<sn-mitre-heatmap-table
							matrixData={matrixData}
							cellWidth={CELL_WIDTH}
							setting={setting[matrixId]}
							search={search}
						/>
					</div>
				}
			</div>
		</div>
	);
};

createCustomElement('sn-mitre-heatmap', {
	styles: style,
	renderer: {type: snabbdom},
	view,
	initialState: {
		loading: false,
		loadingCollectionData: false,
		showFilterPanel: false,
		matricesList: [],
		matrixData: {
			data: [],
		},
		matrixId: null,
		matrixLastRunDate: "",
		hasCveTable: false,
		filters: {
			priority: [
				{id: 1, label: "Critical", type: "priority"},
				{id: 2, label: "High", type: "priority"},
				{id: 3, label: "Moderate", type: "priority"},
				{id: 4, label: "Low", type: "priority"},
				{id: 5, label: "Planning", type: "priority"},
			],
		},
		setting: {},
		defaultSetting: {},
		search: "",
		expand: {
			basic: true,
			advanced: false,
		},
		notification: {
			hideAlert: true,
		},
	},
	behaviors: [preUserData],
	...actionHandler
});
