import {createCustomElement} from '@seismic/core';
import snabbdom, {Fragment} from '@seismic/snabbdom-renderer';
import '@servicenow/now-tooltip';
import { t } from 'sn-translate';
import style from './style.scss';
import {
    generateUrlFilter,
    covert2hex,
} from '../helper.js';

import {
    noneCoverage,
    poorCoverage,
    fairCoverage,
    goodCoverage,
    veryGoodCoverage,
    excellentCoverage,
} from '../assets';

const view = (state, {dispatch}) => {
    const {properties} = state;
    const {
        setting,
        subTechnique,
        tacticId,
    } = properties;

    const setTargetRef = (ref) => dispatch('PROPERTIES_SET', {targetRef: ref});
    const displayCoverage = setting && setting.displayCoverage;

    let overwriteStyle;
    if (displayCoverage)
        overwriteStyle = {color: "black"};

    let overwriteIDStyle;
    if (displayCoverage)
        overwriteIDStyle = {color: "black"};

	return (
        <Fragment>
            <div
                className="mitre-matrix-subtechnique"
                hook-insert={({elm}) => setTargetRef(elm)}
                hook-destroy={() => setTargetRef(null)}
                aria-describedby={`${subTechnique.id}-sub-tooltip`}
                style={
                    displayCoverage && subTechnique.coverageColor && subTechnique.coverageColor.length > 0 ?
                    {
                        backgroundColor: `${covert2hex(subTechnique.coverageColor)}`,
                        borderTop: '1px solid #637274',
                    } : ""
                }
            >
                <div className="technique-name">
                    <a
                        className="technique-name-link"
                        href={`/sn_ti_stix2_attack_pattern.do?sys_id=${subTechnique.id}`}
                        target="_blank"
                        style={overwriteStyle ? overwriteStyle : {width: "100%"}}
                    >
                        {subTechnique.name}
                    </a>
                    {
                        displayCoverage && subTechnique.coverageName === "Excellent" ?
                        <img className="coverage-icon" src={excellentCoverage} /> : ""
                    }
                    {
                        displayCoverage && subTechnique.coverageName === "Very Good" ?
                        <img className="coverage-icon" src={veryGoodCoverage} /> : ""
                    }
                    {
                        displayCoverage && subTechnique.coverageName === "Good" ?
                        <img className="coverage-icon" src={goodCoverage} /> : ""
                    }
                    {
                        displayCoverage && subTechnique.coverageName === "Fair" ?
                        <img className="coverage-icon" src={fairCoverage} /> : ""
                    }
                    {
                        displayCoverage && subTechnique.coverageName === "Poor" ?
                        <img className="coverage-icon" src={poorCoverage} /> : ""
                    }
                    {
                        displayCoverage && subTechnique.coverageName === "None" ?
                        <img className="coverage-icon" src={noneCoverage} /> : ""
                    }
                </div>
                <span className="technique-meta-data" style={setting && setting.displayID ? (overwriteIDStyle ? overwriteIDStyle : {}) : {display: "none"}}>
                    {subTechnique.external_id}
                </span>
                <span className="technique-meta-data" style={setting && setting.displayIncidentCount && !setting.hideFalsePositive ? {} : {display: "none"}}>
                    {
                        subTechnique.incidentCount > 0 ?
                        <a style={overwriteStyle ? {...overwriteStyle, textDecoration: "underline"} : {}} className="count-link" href={`/sn_ti_stix2_m2m_incident_attack_list.do?sysparm_query=attack=${subTechnique.id}${generateUrlFilter(setting && setting.priority, setting && setting.time)}`} target="_blank">{`${t('Security Incidents: ')}${subTechnique.incidentCount}`}</a>
                        : <span style={overwriteStyle ? {...overwriteStyle, textDecoration: "none"} : {}} className="count-link">{`${t('Security Incidents: ')}0`}</span>
                    }
                </span>
                <span className="technique-meta-data" style={setting && setting.displayIncidentCount && setting.hideFalsePositive ? {} : {display: "none"}}>
                    {
                        subTechnique.incidentCountNonFp > 0 ?
                        <a style={overwriteStyle ? {...overwriteStyle, textDecoration: "underline"} : {}} className="count-link" href={`/sn_ti_stix2_m2m_incident_attack_list.do?sysparm_query=attack=${subTechnique.id}${generateUrlFilter(setting && setting.priority, setting && setting.time, true)}`} target="_blank">{`${t('Security Incidents: ')}${subTechnique.incidentCountNonFp}`}</a>
                        : <span style={overwriteStyle ? {...overwriteStyle, textDecoration: "none"} : {}} className="count-link">{`${t('Security Incidents: ')}0`}</span>
                    }
                </span>
                <span className="technique-meta-data" style={setting && setting.displayDetectionRule ? {} : {display: "none"}}>
                    {
                        subTechnique.detRuleCount > 0 ?
                        <a style={overwriteStyle ? {...overwriteStyle, textDecoration: "underline"} : {}} className="count-link" href={`/sn_ti_alert_rules_mitre_attack_technique_mapping_list.do?sysparm_query=technique=${subTechnique.id}^tactic=${tacticId}`} target="_blank">{`${t('Detection Rule: ')}${subTechnique.detRuleCount}`}</a>
                        : <span style={overwriteStyle ? {...overwriteStyle, textDecoration: "none"} : {}} className="count-link">{`${t('Detection Rule: ')}0`}</span>
                    }
                </span>
                <span className="technique-meta-data" style={setting && setting.displayVulCount ? {} : {display: "none"}}>
                    {
                        subTechnique.vulCount > 0 ?
                        <a style={overwriteStyle ? {...overwriteStyle, textDecoration: "underline"} : {}} className="count-link" href={`/sn_ti_stix2_m2m_attack_nvd_list.do?sysparm_query=attack=${subTechnique.id}`} target="_blank">{`${t('CVEs: ')}${subTechnique.vulCount}`}</a>
                        : <span style={overwriteStyle ? {...overwriteStyle, textDecoration: "none"} : {}} className="count-link">{`${t('CVEs: ')}0`}</span>
                    }
                </span>
            </div>
            <now-tooltip
                id={`${subTechnique.id}-sub-tooltip`}
                target-ref={properties.targetRef}
                position={['center-end center-start', 'center-start center-end']}
                content={`${t("Sub-technique ID")} ${subTechnique.external_id}`}
            />
        </Fragment>
    );
};

createCustomElement('sn-mitre-heatmap-subtechnique-cell', {
    renderer: {type: snabbdom},
    styles: style,
	view,
	properties: {
        setting: {},
        subTechnique: {},
        tacticId: "",
        targetRef: {default: null}
    }
});