import { createCustomElement } from '@seismic/core';
import snabbdom, { Fragment, createRef } from '@seismic/snabbdom-renderer';
import { t } from 'sn-translate';
import style from './style.scss';

import './sn-mitre-heatmap-technique-cell';
import './sn-mitre-heatmap-legend';

const view = (state) => {
    const {
        cellWidth,
        setting,
        matrixData,
        search,
    } = state.properties;

    const searchToken = search && search.toLowerCase();
    const displayLegend = matrixData && matrixData.legendData && setting && (setting.displayCoverage === "true" || setting.displayCoverage === true);

    // data massage here
    matrixData.data.forEach(tactic => {
        let techniqueCount = tactic.pattern.length;
        tactic.pattern.forEach((technique, index) => {
            technique.subTechniques = [];
            if (search && technique.name.toLowerCase().indexOf(searchToken) === -1 && technique.external_id.toLowerCase().indexOf(searchToken) === -1)
                techniqueCount--;

            // skip the technique if the technique is sub-technique
            // and has already been added to the array
            if (technique.hide) return;
            else if (technique.isParentTechnique) {
                // if it is parent technique
                // search for its sub-technique among the rest techniques
                let subTechniqueCount = Object.keys(technique.children).length;
                let searchLength = Math.min(subTechniqueCount + index + 1, tactic.pattern.length);
                for (let i = index + 1; i < searchLength; i++) {
                    let tempTechnique = tactic.pattern[i];
                    let isOneOfSubTechnique = technique.children[tempTechnique.id];
                    if (isOneOfSubTechnique) {
                        technique.subTechniques.push(tempTechnique);
                        // hide this sub-technique since it has already been added to the array
                        tempTechnique.hide = true;
                    }
                }
            }
        });

        tactic.techniqueCount = techniqueCount;
    });

	return (
        <Fragment>
            <div className="mitre-matrix-table-header-container">
                <div className="mitre-matrix-table-row">
                    <div style={{width: `${15}px`, minWidth: `${15}px`}}></div>
                    {
                        matrixData.data.map(item => 
                            <span className="mitre-matrix-table-col tactic-name" style={{width: `${cellWidth}px`, minWidth: `${cellWidth}px`}}>{item.name}</span>
                        )
                    }
                    <div style={{width: `${15}px`, minWidth: `${15}px`}}></div>
                </div>
                <div className="mitre-matrix-table-row">
                    <div style={{width: `${15}px`, minWidth: `${15}px`}}></div>
                    {
                        matrixData.data.map(item => 
                            <span className="mitre-matrix-table-col technique-total-count" style={{width: `${cellWidth}px`, minWidth: `${cellWidth}px`}}>{item.techniqueCount} {t('techniques')}</span>
                        )
                    }
                    <div style={{width: `${15}px`, minWidth: `${15}px`}}></div>
                </div>
            </div>
            {
                displayLegend ? <sn-mitre-heatmap-legend legendData={matrixData.legendData} /> : <Fragment></Fragment>
            }
            <div className="mitre-matrix-table-body-container" style={{marginTop: displayLegend ? "210px" : "170px"}}>
                <div style={{width: `${15}px`, minWidth: `${15}px`}}></div>
                {
                    matrixData.data.map(tactic => 
                        <div className="mitre-matrix-tactic-group" style={{width: `${cellWidth}px`, minWidth: `${cellWidth}px`}}>
                            {
                                tactic.pattern.map(technique => 
                                    technique.hide ? "" :
                                    <sn-mitre-heatmap-technique-cell
                                        setting={setting}
                                        search={search}
                                        technique={technique}
                                        tacticId={tactic.id}
                                    />
                                )
                            }
                        </div>
                    )
                }
                <div style={{width: `${15}px`, minWidth: `${15}px`}}></div>
            </div>
        </Fragment>
    );
};

createCustomElement('sn-mitre-heatmap-table', {
    styles: style,
	renderer: {type: snabbdom},
	view,
	properties: {
        cellWidth: 0,
        setting: {},
        matrixData: {},
        search: "",
    }
});