import { createCustomElement } from '@seismic/core';
import snabbdom, { Fragment } from '@seismic/snabbdom-renderer';
import { t } from 'sn-translate';
import {
    covert2hex,
} from '../../helper.js';
import style from './style.scss';

import {
    noneCoverage,
    poorCoverage,
    fairCoverage,
    goodCoverage,
    veryGoodCoverage,
    excellentCoverage,
} from '../../assets';

const view = (state) => {

    const {properties} = state;
	
	return (
        <div id="mitre-coverage-legend-container">
            <div style={{width: `${20}px`, minWidth: `${20}px`}}></div>
            <div className="mitre-coverage-legend-box">
                <div className="mitre-coverage-legend-text"><b>{t("Coverage Type")}:</b></div>
                {
                    properties.legendData.map(legend =>
                        <div className="mitre-coverage-legend">
                            <div className="mitre-coverage-legend-color" style={{backgroundColor: `${covert2hex(legend.color)}`}}>
                            {
                                legend && legend.name === "Excellent" ?
                                <img height="10" width="10" src={excellentCoverage} /> : ""
                            }
                            {
                                legend && legend.name === "Very Good" ?
                                <img height="10" width="10" src={veryGoodCoverage} /> : ""
                            }
                            {
                                legend && legend.name === "Good" ?
                                <img height="10" width="10" src={goodCoverage} /> : ""
                            }
                            {
                                legend && legend.name === "Fair" ?
                                <img height="10" width="10" src={fairCoverage} /> : ""
                            }
                            {
                                legend && legend.name === "Poor" ?
                                <img height="10" width="10" src={poorCoverage} /> : ""
                            }
                            {
                                legend && legend.name === "None" ?
                                <img height="10" width="10" src={noneCoverage} /> : ""
                            }
                            </div>
                            <div className="mitre-coverage-legend-name">{legend.name}</div>
                        </div>
                    )
                }
            </div>
            <div style={{width: `${20}px`, minWidth: `${20}px`}}></div>
        </div>
    );
};

createCustomElement('sn-mitre-heatmap-legend', {
    styles: style,
	renderer: {type: snabbdom},
	view,
	properties: {
        legendData: [],
    }
});